import "./iconText.css"

import { Icon as MaterialIcon } from "@material-ui/core"
import React from "react"

interface IIconTextProps {
  Icon: React.ComponentType
  text: string[]
  color?: string
}

export const IconText = ({ text, Icon, color }: IIconTextProps) => (
  <div
    style={{
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      width: "fit-content",
    }}
  >
    {/* <MaterialIcon>{<Icon style={{ fontSize: 40 }} />}</MaterialIcon> */}
    {/*@ts-ignore*/}
    <Icon className="icon" style={{ color: color || "black" }} />
    {text.map(t => (
      <div className="icon-text">{t}</div>
    ))}
  </div>
)
